import React from "react";


export default function Component() {
  return (
    <div className="content">
      <div className="content__text">
        <p>Основные методы жизненного цикла:</p>
        <ol className="list-color">
          <li>
            <strong>constructor()</strong>
            <p>
              функция, позволяющая создать экземпляр класса (конструктор, в
              котором происходит начальная инициализация компонента)
            </p>
          </li>
          <li>
            <strong> render()</strong>
            <p>
              рендеринг компонента (если shouldComponentUpdate возвращает true)
            </p>
          </li>
          <li>
            <strong>componentDidMount()</strong>
            <p>
              <strong>КОМПОНЕНТ БЫЛ ОТОБРАЖЕН НА СТРАНИЦЕ. </strong>вызывается
              после того, как компонент вмонтировался в дом, после рендеринга
              компонента. Здесь можно выполнять запросы к удаленным ресурсам.
              Эта функция будет вызвана лишь раз во всем жизненном цикле данного
              компонента и будет сигнализировать, что компонент и все его
              дочерние компоненты отрисовались без ошибок.
            </p>
          </li>
          <li>
            <strong>omponentDidUpdate()</strong>
            <p>
              <strong>В КОМПОНЕНТЕ ПРОИЗОШЛИ ИЗМЕНЕНИЯ.</strong> вызывается
              после ререндера (рендер дидмаунт -к- рендер апдейт):
              componentDidUpdate(prevProps, prevState)** вызывается сразу после
              обновления компонента (если shouldComponentUpdate возвращает
              true). В качестве параметров передаются старые значения объектов
              props и state.
            </p>
          </li>
          <li>
            <strong>componentWillUnmount()</strong>
            <p>
              <strong>КОМПОНЕНТ БУДЕТ УДАЛЕН.</strong> вызывается перед
              удалением и чистит за собой асинхронные запросы и таймеры (можно
              посмотреть разницу времени нахождения на странице): вызывается
              перед удалением компонента из DOM. Используйте эту функцию для
              «очистки» после компонента, если он использует таймеры
              (setTimeout, setInterval), открывает сокеты или производит любые
              операции, которые нуждаются в закрытии или удалении.
            </p>
          </li>
          <li>
            <strong>shouldComponentUpdate(nextProps, nextState)</strong>
            <p></p>
            вызывается каждый раз при обновлении объекта props или state. В
            качестве параметра передаются новый объект props и state. Эта
            функция должна возвращать true (надо делать обновление) или false
            (игнорировать обновление). По умолчанию возвращается true. Но если
            функция будет возвращать false, то тем самым мы отключим обновление
            компонента, а последующие функции не будут срабатывать.
          </li>
        </ol>
        <p>
          Для использования методов жизненного цикла в функциональных
          компонентах предназначен хук useEffect.
        </p>
        <p>
          Здесь рассмотрена теоретическая сторона вопроса, примеры кода есть в
          главе о useEffect.
        </p>
      </div>
    </div>
  );
}
