import React from "react";
import "./ExampleStyle.css"

export default class StateExampleClass extends React.Component {
  state = {
    count: 0,
  };

  render() {
    return (
      <div className="example">
        <div className="result">
            {this.state.count}
            </div>

        <div className="btns">
          <button
            className="btn btn_1"
            onClick={() => this.setState({ count: 5 })}
          >
            Just 5
          </button>
          <button
            className="btn btn_2"
            onClick={() =>
              this.setState(
                (state) => ({ count: this.state.count + 1 }),
                () => console.log(this.state)
              )
            }
          >
            Click! and get + 1
          </button>
        </div>
      </div>
    );
  }
}
